import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  Tabel,
  // getToday,
  Swal,
  moment,
  formatDate,
  setItem,
  getItem,
  ToastNotification,
  ModalGlobal,
  showModal,
  ReanderSelect,
  Field,
  hideModal,
  PanelBody,
} from "../../../components/helpers/library";
import { getData, getData2, putData2 } from "../../../config/axios";
import HeadBatalTitipan from "./HeadBatalTitipan";
import Loading from "react-fullscreen-loading";

class BatalTitipan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      batal: false,
      datatitipan: [],
      databaki: [],
      columns: [
        {
          dataField: "no_titip_group",
          text: "No Titip",
          footerAttrs: {
            colSpan: "4",
          },
          footer: "Total",
        },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },

        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "nama_customer",
          text: "Nama Customer",
        },

        // {
        //   dataField: "nama_atribut",
        //   text: "Nama Atribut",
        // },
        // {
        //   dataField: "berat",
        //   text: "Berat",
        //   formatter: (cell) => {
        //     return <span>{cell.toFixed(3)}</span>;
        //   },
        //   footer: columnData => columnData.reduce((acc, item) => acc + item, 0).toFixed(3)
        // },
        // {
        //   dataField: "berat_atribut",
        //   text: "Berat Atribut",
        //   formatter: (cell) => {
        //     return <span>{cell.toFixed(3)}</span>;
        //   },
        //   footer: columnData => columnData.reduce((acc, item) => acc + item, 0).toFixed(3)
        // },

        // {
        //   dataField: "harga_atribut",
        //   text: "Harga Atribut",
        //   formatter: (cell) => {
        //     return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
        //   },
        //   footer: columnData => parseInt(columnData.reduce((acc, item) => acc + item, 0)).toLocaleString("kr-KO")

        // },
        // {
        //   dataField: "harga_jual",
        //   text: "Harga Jual",
        //   formatter: (cell) => {
        //     return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
        //   },
        //   footer: columnData => parseInt(columnData.reduce((acc, item) => acc + item, 0)).toLocaleString("kr-KO")

        // },
        {
          dataField: "total_dp",
          text: "Total Dp",
          formatter: (rowcontent, row, index) => {
            return (
              <span>{parseInt(row.total_dp).toLocaleString("kr-KO")}</span>
            );
          },

          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },

        {
          dataField: "harga_total",
          text: "Total Harga",
          formatter: (cell) => {
            return <span> {parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    disabled={row.status_titipan !== "FINIS" ? false : true}
                    data-tooltip={
                      row.status_titipan !== "FINIS"
                        ? "Batal Titipan"
                        : "Tidak Bisa Dibatal"
                    }
                    onClick={() => this.showPilihBaki(row.no_titip_group)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-window-close"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  batalTitipan() {
   
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Membatalkan Titipan Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        this.setState({
          batal: true,
        });
        let tanggal = getItem("tgl_laporan");
        putData2("titip/cancel", {
          no_titip_group: this.state.no_titip_group,
          kode_toko: this.state.kode_baki,
        })
          .then((res) => {
            this.setState({
              batal: false,
            });
            ToastNotification("success", "Titipan Berhasil Di Batal");
            this.props.dispatch(hideModal());
            getData2(
              "titip/date/" + tanggal.tgl_awal + "/" + tanggal.tgl_akhir
            ).then((res) => {
              // console.log(res.data);
              let hasil = [];
              res.data.filter((item, index) => {
                // let status = res.data.findIndex(x => x.id === item.id) === index;
                let row = {
                  no_titip_group: item.no_titip_group,
                  kode_barcode: item.kode_barcode,
                  nama_barang: item.nama_barang,
                  kode_customer: item.kode_customer,
                  nama_customer: item.nama_customer,
                  nama_atribut: item.nama_atribut,
                  no_hp: item.no_hp,
                  alamat_customer: item.alamat_customer,
                  berat_atribut: item.berat_atribut,
                  harga_atribut: item.harga_atribut,
                  ongkos: item.ongkos,
                  harga_total: item.harga_total,
                  // status : res.data.findIndex(x => x.no_titip_group === item.no_titip_group) === index,
                  total_dp:
                    res.data.findIndex(
                      (x) => x.no_titip_group === item.no_titip_group
                    ) === index
                      ? item.total_dp
                      : 0,
                };
                hasil.push(row);
                return row;
              });
              this.setState({
                datatitipan: hasil,
              });
            });
          })
          .catch((error) => {
            ToastNotification(
              "info",
              /conflict/i.test(error.response.data.message || "Error")
                ? "Terjadi Kesalahan Saat Mengirim Data"
                : error.response.data.message
            );
          });
      } else {
        // console.log("test");
        this.setState({
          batal: false,
        });
      }
    });
  }

  showPilihBaki(no_titip_group) {
    this.setState({
      no_titip_group: no_titip_group,
    });
    this.props.dispatch(showModal());
  }

  componentDidMount() {
    getData("baki/ready").then((res) => {
      this.setState({
        databaki: res.data,
      });
    });
  }
  handleSubmit(data) {
    // console.log(data);
    this.setState({
      isLoading: true,
    });

    getData2("titip/date/" + data.tgl_awal + "/" + data.tgl_akhir)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            isLoading: false,
          });
        } else {
          ToastNotification("success", "Data Titipan Tersedia");
          let data_post = {
            tgl_awal: formatDate(
              moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
            ),
            tgl_akhir: formatDate(
              moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
            ),
          };
          let hasil = [];
          res.data.filter((item, index) => {
            // let status = res.data.findIndex(x => x.id === item.id) === index;
            let row = {
              no_titip_group: item.no_titip_group,
              kode_barcode: item.kode_barcode,
              nama_barang: item.nama_barang,
              kode_customer: item.kode_customer,
              nama_customer: item.nama_customer,
              nama_atribut: item.nama_atribut,
              no_hp: item.no_hp,
              alamat_customer: item.alamat_customer,
              berat_atribut: item.berat_atribut,
              harga_atribut: item.harga_atribut,
              ongkos: item.ongkos,
              harga_total: item.harga_total,
              // status : res.data.findIndex(x => x.no_titip_group === item.no_titip_group) === index,
              total_dp:
                res.data.findIndex(
                  (x) => x.no_titip_group === item.no_titip_group
                ) === index
                  ? item.total_dp
                  : 0,
            };
            hasil.push(row);
            return row;
          });
          // console.log(hasil);
          this.setState({
            isLoading: false,
            datatitipan: hasil,
          });
          setItem("tgl_laporan", data_post);
        }
      })
      .catch((err) => {
        ToastNotification("info", err.response=== undefined ? "Terjadi Kesalahan Saat Request Data" : err.response.data.message);
        this.setState({
          isLoading: false,
        });
      });
  }

  render() {
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              {row.kode_customer === "NONMEMBER"
                ? ""
                : "kode Customer :" +
                  row.kode_customer +
                  (
                    <>
                      {" "}
                      <br />{" "}
                    </>
                  )}
              No Hp : {row.no_hp} <br />
              Alamat Customer : {row.alamat_customer} <br />
              Nama Atribut : {row.nama_atribut} <br />
              Betat Atribut : {row.berat_atribut.toFixed(3)} <br />
              Harga Atribut : {row.harga_atribut.toLocaleString("kr-KO")} <br />
              Ongkos : {row.ongkos.toLocaleString("kr-KO")} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Penjualan</Link>
          </li>
          <li className="breadcrumb-item active">Lihat Titipan</li>
        </ol>
        <h1 className="page-header">Lihat Titipan </h1>
        <Panel>
          <PanelHeader>Lihat Titipan</PanelHeader>
          <br />
          {/* Blank Page */}
          <PanelBody>
            <HeadBatalTitipan
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
            <Tabel
              keyField="kode_barcode"
              exportCsv={false}
              data={this.state.datatitipan}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Barang Kosong"
              expandRow={expandRow}
            />
          </PanelBody>

          <br />
          {/* End Tambah Blank Page  */}
        </Panel>
        <ModalGlobal
          title={"Pilih Baki"}
          size="l"
          content={
            <>
              <div className="col-lg-12">
                <Field
                  name="kode_baki"
                  component={ReanderSelect}
                  options={this.state.databaki
                    .filter((list) => list.kode_baki !== undefined)
                    .map((list) => {
                      let data = {
                        value: list.kode_baki,
                        name: list.kode_baki,
                      };
                      return data;
                    })}
                  onChange={(e) =>
                    this.setState({
                      kode_baki: e,
                    })
                  }
                  label="Kode Baki"
                  placeholder="Silahkan Pilih Kode Lokasi"
                />
              </div>
              <div className="col-lg-12">
                <button
                  className="btn btn-primary"
                  disabled={this.state.batal}
                  onClick={() => this.batalTitipan()}
                  type="button"
                >
                  {this.state.batal ? (
                    <>
                      <i className="fas fa-spinner fa-spin"></i> &nbsp; Batal
                    </>
                  ) : (
                    "Batal"
                  )}
                </button>
              </div>
            </>
          }
        />
        {this.state.batal ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
        {this.state.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </div>
    );
  }
}

BatalTitipan = reduxForm({
  form: "BatalTitipan",
  enableReinitialize: true,
})(BatalTitipan);
export default connect()(BatalTitipan);
